<template>
  <div class="one mescroll">
    <div class="mid" id="mid">
      <p class="title">根据国家要求，请提供准确的实名信息</p>
      <van-cell-group>
        <van-cell title="套餐" is-link arrow-direction="down" :value-class="setMealName?'color':''" :value="setMealName || '请选择套餐'" @click="showSetMeal=true" />
        <van-field v-model="name" label="姓名" placeholder="请输入姓名" @blur="blur" />
        <van-field v-model="telno" label="手机号" type="number" placeholder="输入手机号便于快递小哥联系" @blur="blur" />
        <van-field v-model="idCardNo" label="身份证" placeholder="请输入身份证" @blur="blur" />
        <van-cell title="省市区" is-link arrow-direction="down" :value-class="areaName?'color':''" :value="areaName || '请选择省市区'" @click="showAddress=true" />
        <van-field v-model="addr" label="详细收货地址" placeholder="请输入详细收货地址" @blur="blur" />
      </van-cell-group>
      <div class="flex">
        <van-checkbox v-model="checked" shape="square" />
        <div class="text">
          我已阅读并同意
          <span @click="agreementShow=true">《用户身份授权协议》</span>
        </div>
      </div>
      <div class="btn" @click="submit">立即领取</div>
    </div>
    <van-popup v-model="showSetMeal" position="bottom">
      <van-picker title="套餐" show-toolbar :columns="columns" value-key="vName" @confirm="setMealConfirm" @cancel="showSetMeal = false" />
    </van-popup>
    <van-popup v-model="showAddress" position="bottom">
      <van-area :area-list="areaList" :value="areaCode" @confirm="areaConfirm" @cancel="showAddress = false" />
    </van-popup>
    <van-popup v-model="agreementShow" round>
      <div class="agreementMid">
        <div class="close" @click="agreementShow=false">×</div>
        <div v-html="agreement">{{agreement}}</div>
      </div>
    </van-popup>
  </div>
</template>
<script>
import {
  CellGroup,
  Cell,
  Field,
  Popup,
  Area,
  Checkbox,
  Picker,
  Dialog,
} from "vant";
import areaList from "@/utils/AreaList";
import { cardSubmit, getGoodsDetail, getPage } from "@/api";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [Checkbox.name]: Checkbox,
    [Picker.name]: Picker,
  },
  data() {
    return {
      name: "",
      telno: "",
      idCardNo: "",
      areaName: "",
      areaCode: "",
      addr: "",
      areaList: areaList,
      showAddress: false,
      checked: true,
      list: [],
      msg: "",
      pics: [],
      showSetMeal: false,
      columns: [],
      setMealName: "",
      setMealId: "",
      agreementShow: false,
      agreement: "",
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    setMealConfirm(value) {
      this.showSetMeal = false;
      this.setMealName = value.vName;
      this.setMealId = value.vCode;
    },
    areaConfirm(obj) {
      this.showAddress = false;
      this.areaName = `${obj[0].name} ${obj[1].name} ${obj[2].name}`;
      this.areaCode = obj[2].code;
    },
    submit() {
      if (!this.setMealId) {
        this.$toast("请选择套餐");
        return;
      }
      if (!this.name) {
        this.$toast("请输入姓名");
        return;
      }
      if (!this.telno) {
        this.$toast("请输入手机号");
        return;
      }
      if (!this.$utils.isMobile(this.telno)) {
        this.$toast("请输入正确的手机号");
        return;
      }
      if (!this.areaCode) {
        this.$toast("请选择省市区");
        return;
      }
      if (!this.addr) {
        this.$toast("请输入详细收货地址");
        return;
      }
      if (!this.checked) {
        this.$toast("请同意《用户身份授权协议》");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      cardSubmit({
        gcode: this.$route.query.id,
        certName: this.name,
        contactNum: this.telno,
        certNo: this.idCardNo,
        postDistrictCode: this.areaCode,
        postAddr: this.addr,
        parentCode: this.$route.query.parentId || "",
        vcode: this.setMealId,
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$router.push({
            name: "cardThreeResult",
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getPage({
        id: "1295522665976070144",
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.agreement = res.data.tMsg.replace(
            /(<img[\s\S]+?)src=(['"])/g,
            '<img src="' + this.$config.http
          );
        } else {
          this.$toast(res.msg);
        }
      });
      if (this.$route.query.id) {
        getGoodsDetail({
          gcode: this.$route.query.id,
          shopUrl: "",
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.pics = res.data.gimg.split(",");
            this.columns = res.data.scEcomGoodsValList;
            this.setMealName = res.data.scEcomGoodsValList[0].vName;
            this.setMealId = res.data.scEcomGoodsValList[0].vCode;
          } else {
            this.$toast(res.msg);
          }
        });
      }
    },
  },
  mounted() {
    document.title = this.$route.query.title;
    this.loadData();
  },
};
</script>
<style lang="scss" scoped>
.one {
  background: #fff;
  .mid {
    .title {
      text-align: center;
      padding: 15px 0;
      line-height: 30px;
    }
    .flex {
      display: flex;
      align-items: center;
      padding: 10px;
      .text {
        flex: 2;
        margin: 0 0 0 10px;
        line-height: 25px;
        span {
          color: #20a0ff;
        }
      }
    }
    .btn {
      margin: 10px;
      background: #20a0ff;
      color: #fff;
      border-radius: 5px;
      text-align: center;
      line-height: 45px;
    }

    .van-cell__title {
      width: 90px;
      flex: none;
    }
    .van-cell__value {
      text-align: left;
    }
    .van-checkbox__icon--checked .van-icon {
      border-color: #20a0ff !important;
      background-color: #20a0ff !important;
    }
    .color {
      color: #111;
    }
  }

  .agreementMid {
    width: 85vw;
    padding: 10px;
    height: 85vh;
    overflow-y: auto;
    position: relative;
    .close {
      position: absolute;
      right: 15px;
      top: 5px;
      width: 10px;
      height: 10px;
      color: #999;
      font-size: 20px;
    }
  }
}
</style>
